/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { addQueryArgs } from '@wordpress/url';
import { _x } from '@wordpress/i18n';
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * External dependencies
 */
import classnames from 'classnames';
import { AsyncPaginate } from 'react-select-async-paginate';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { GroupBase } from 'react-select';
import type { Maybe, Menu, PaginatedResults } from '@nab/types';

/**
 * Internal dependencies
 */
import './style.scss';

export type MenuSearcherProps = {
	readonly className?: string;
	readonly value: Maybe< number | string >;
	readonly onChange: ( val: Maybe< number > ) => void;
	readonly disabled?: boolean;
	readonly placeholder?: string;
};

type MenuOption = {
	readonly value: number;
	readonly label: string;
};

type AdditionalArgs = {
	readonly page: number;
};

export const MenuSearcher = ( {
	className,
	disabled,
	onChange,
	placeholder: defaultPlaceholder,
	value,
}: MenuSearcherProps ): JSX.Element => {
	const id = numberify( value );
	const menu = useMenu( id );
	const isLoading = useIsLoadingMenu( id );
	const placeholder = usePlaceholder( id, defaultPlaceholder );
	const loadOptions = useOptionLoader();

	const label = menu?.name;
	const selectedOption =
		! isLoading && id ? { value: id, label: label ?? '' } : undefined;

	return (
		<AsyncPaginate<
			MenuOption,
			GroupBase< MenuOption >,
			AdditionalArgs,
			false
		>
			className={ classnames( [
				className,
				{
					'nab-menu-searcher': true,
					'nab-menu-searcher--is-loading': isLoading,
				},
			] ) }
			defaultOptions
			isDisabled={ disabled || isLoading }
			cacheUniqs={ [ 'nab-wp-menu' ] }
			loadOptions={ loadOptions }
			value={ selectedOption }
			onChange={ ( option ) =>
				onChange( Math.abs( option?.value ?? 0 ) || 0 )
			}
			additional={ { page: 1 } }
			placeholder={ placeholder }
		/>
	);
};

// =====
// HOOKS
// =====

const useMenu = ( value?: number ) =>
	useSelect( ( select ) => select( NAB_DATA ).getMenu( value || 0 ) );

const useIsLoadingMenu = ( value?: number ) => {
	const menu = useMenu( value );
	return !! value && ! menu;
};

const usePlaceholder = (
	value?: number,
	defaultPlaceholder?: string
): string => {
	const isLoading = useIsLoadingMenu( value );
	if ( isLoading ) {
		return _x( 'Loading…', 'text', 'nelio-ab-testing' );
	} //end if

	return (
		defaultPlaceholder || _x( 'Select a menu…', 'user', 'nelio-ab-testing' )
	);
};

const useOptionLoader = () => {
	const { receiveMenus } = useDispatch( NAB_DATA );
	const cacheEntities = ( entities: ReadonlyArray< Menu > ) =>
		entities.length && receiveMenus( entities );

	return ( query: string, _, { page }: AdditionalArgs ) =>
		apiFetch( {
			path: addQueryArgs( '/nab/v1/menu/search', { query } ),
		} ).then( ( data: PaginatedResults< ReadonlyArray< Menu > > ) => {
			cacheEntities( data.results );
			return {
				options: data.results.map( ( option ) => ( {
					value: option.id,
					label: option.name,
				} ) ),
				hasMore: data.pagination.more,
				additional: {
					page: page + 1,
				},
			};
		} );
};

// =======
// HELPERS
// =======

const numberify = ( x: Maybe< number | string > ): Maybe< number > => {
	if ( undefined === x ) {
		return;
	} //end if

	if ( 'number' === typeof x ) {
		return x;
	} //end if

	const n = Number.parseInt( x );
	return Number.isNaN( n ) ? undefined : n;
};
