/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { addQueryArgs } from '@wordpress/url';
import { _x } from '@wordpress/i18n';
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * External dependencies
 */
import classnames from 'classnames';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { EntityInstance, Maybe, PaginatedResults } from '@nab/types';

/**
 * Internal dependencies
 */
import './style.scss';
import { PostOption } from './post-option';
import { AsyncSelectControl } from '../async-select-control';

export type PostSearcherProps = {
	readonly id?: string;
	readonly className?: string;
	readonly type?: string;
	readonly value: Maybe< number | string >;
	readonly onChange: ( val: Maybe< number > ) => void;
	readonly disabled?: boolean;
	readonly placeholder?: string;
} & OptionLoaderProps;

type OptionLoaderProps = {
	readonly type: string;
	readonly perPage?: number;
	readonly filter?: ( p: EntityInstance ) => boolean;
};

export const PostSearcher = ( {
	id,
	className,
	disabled,
	onChange,
	placeholder: defaultPlaceholder,
	value,
	type = 'post',
	...loaderProps
}: PostSearcherProps ): JSX.Element => {
	const postId = numberify( value );
	const post = usePost( type, postId );
	const isLoading = useIsLoadingPost( type, postId );
	const placeholder = usePlaceholder(
		{ type, id: postId },
		defaultPlaceholder
	);

	const label = post?.title;
	const selectedOption =
		! isLoading && postId
			? { type, value: postId, label: label ?? '' }
			: undefined;

	const loadOptions = useOptionLoader( { ...loaderProps, type } );

	return (
		<AsyncSelectControl
			id={ id }
			className={ classnames( [
				className,
				{
					'nab-post-searcher': true,
					'nab-post-searcher--is-loading': isLoading,
				},
			] ) }
			components={ { Option: PostOption } }
			disabled={ disabled || isLoading }
			cacheUniqs={ [ type ] }
			loadOptions={ loadOptions }
			value={ selectedOption }
			onChange={ ( option ) =>
				onChange( Math.abs( option?.value ?? 0 ) || 0 )
			}
			additional={ { page: 1 } }
			placeholder={ placeholder }
		/>
	);
};

// =====
// HOOKS
// =====

const usePost = ( type: string, value?: number ) =>
	useSelect( ( select ) =>
		select( NAB_DATA ).getEntityRecord( type, value || 0 )
	);

const useIsLoadingPost = ( type: string, value?: number ) => {
	const post = usePost( type, value );
	return !! value && ! post;
};

const usePlaceholder = (
	{ type, id }: { type: string; id?: number },
	defaultPlaceholder?: string
): string => {
	const isLoading = useIsLoadingPost( type, id );
	if ( isLoading ) {
		return _x( 'Loading…', 'text', 'nelio-ab-testing' );
	} //end if

	if ( defaultPlaceholder ) {
		return defaultPlaceholder;
	} //end if

	switch ( type ) {
		case 'page':
			return _x( 'Select a page…', 'user', 'nelio-ab-testing' );
		case 'post':
			return _x( 'Select a post…', 'user', 'nelio-ab-testing' );
		case 'product':
			return _x( 'Select a product…', 'user', 'nelio-ab-testing' );
		default:
			return _x( 'Select…', 'user', 'nelio-ab-testing' );
	} //end switch
};

const useOptionLoader = ( {
	type,
	perPage = 50,
	filter = () => true,
}: OptionLoaderProps ) => {
	const { receiveEntityRecords } = useDispatch( NAB_DATA );

	const cacheEntities = ( entities: ReadonlyArray< EntityInstance > ): void =>
		void ( entities.length && receiveEntityRecords( type, entities ) );

	return ( query: string, _, { page }: { page: number } ) =>
		apiFetch( {
			path: addQueryArgs( '/nab/v1/post/search', {
				query,
				type,
				page,
				per_page: perPage,
			} ),
		} ).then(
			( data: PaginatedResults< ReadonlyArray< EntityInstance > > ) => {
				cacheEntities( data.results );
				const results = data.results.filter( filter );
				return {
					options: results.map( ( option ) => ( {
						value: option.id,
						label: option.title,
						type,
					} ) ),
					hasMore: data.pagination.more,
					additional: {
						page: page + 1,
					},
				};
			}
		);
};

// =======
// HELPERS
// =======

const numberify = ( x: Maybe< number | string > ): Maybe< number > => {
	if ( undefined === x ) {
		return;
	} //end if

	if ( 'number' === typeof x ) {
		return x;
	} //end if

	const n = Number.parseInt( x );
	return Number.isNaN( n ) ? undefined : n;
};
