/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { useSelect } from '@wordpress/data';
import { _x, sprintf } from '@wordpress/i18n';

/**
 * External dependencies
 */
import classnames from 'classnames';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import { numberFormat } from '@nab/i18n';

/**
 * Internal dependencies
 */
import './style.scss';

export const QuotaMeter = (): JSX.Element => {
	const { availableQuota, isLoading, percentage } = useQuota();

	return (
		<div className="nab-quota-meter">
			<p className="nab-quota-meter__title">
				{ _x( 'Quota', 'text', 'nelio-ab-testing' ) }

				<span className="nab-quota-meter__amount">
					{ !! isLoading &&
						_x( 'Loading…', 'text', 'nelio-ab-testing' ) }

					{ ! isLoading &&
						availableQuota > 0 &&
						sprintf(
							/* translators: quota number */
							_x(
								'%s available page views',
								'text',
								'nelio-ab-testing'
							),
							numberFormat( availableQuota )
						) }

					{ ! isLoading &&
						availableQuota === 0 &&
						_x(
							'There are no more available page views',
							'text',
							'nelio-ab-testing'
						) }
				</span>
			</p>

			{ 100 === percentage ? (
				<div className="nab-quota-meter__bar-container">
					<span
						className={ classnames(
							'nab-quota-meter__bar',
							`nab-quota-meter__bar--width-${ percentage.toFixed(
								0
							) }`
						) }
					></span>
				</div>
			) : (
				<div className="nab-quota-meter__bar-container">
					<TransitionGroup>
						<CSSTransition
							classNames="nab-quota-meter--animation"
							appear={ true }
							enter={ false }
							exit={ false }
							timeout={ 2500 }
						>
							<span
								className={ classnames(
									'nab-quota-meter__bar',
									`nab-quota-meter__bar--width-${ percentage.toFixed(
										0
									) }`
								) }
							></span>
						</CSSTransition>
					</TransitionGroup>
				</div>
			) }
		</div>
	);
};

// =====
// HOOKS
// =====

const useQuota = () =>
	useSelect( ( select ) => {
		const { getQuota, getQuotaPerMonth, getQuotaExtra } = select(
			NAB_DATA
		);

		const quota = getQuota();
		const quotaExtra = getQuotaExtra();
		const availableQuota = quota + quotaExtra;

		const quotaPerMonth = getQuotaPerMonth();
		const percentage = ( 100 * ( availableQuota + 0.1 ) ) / quotaPerMonth;

		return {
			availableQuota,
			percentage: Math.max( 1, Math.min( percentage, 100 ) ),
			isLoading: select( NAB_DATA ).isResolving( 'getQuota' ),
		};
	} );
